.selected-date {
    background: rgba(0,128,128, 1);
    color: white;
}

.in-range {
    background: rgba(0,128,128, 0.3);
}

.date-cell {
    flex: 1;
    cursor: pointer;
    text-align: center;
    padding: 5px 0;
}

.default-hover:hover {
    background: rgb(202, 198, 198);
}

.filler-date {
    background-color: #fff;
    border-color: transparent;
    color: #999;
}